import React from 'react';
import '../styles/BentoGridSection.css';

const BentoGridSection = () => {
  const items = [
    { id: 1, name: 'Feature 1', description: 'Innovative solutions' },
    { id: 2, name: 'Feature 2', description: 'Seamless integration' },
    { id: 3, name: 'Feature 3', description: 'High performance' },
    { id: 4, name: 'Feature 4', description: 'User-friendly design' },
    { id: 5, name: 'Feature 5', description: 'Scalable architecture' },
    { id: 6, name: 'Feature 6', description: 'Secure platform' },
  ];

  return (
    <section className="bento-grid-section">
      <div className="bento-grid">
        {items.map((item) => (
          <div key={item.id} className="bento-item">
            <h3 className="bento-title">{item.name}</h3>
            <p>{item.description}</p>
          </div>
        ))}
      </div>
    </section>
  );
};

export default BentoGridSection;