import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import '../styles/ScrollRevealSection.css';

const ScrollRevealSection = () => {
  const textRefs = useRef([]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            gsap.to(entry.target, {
              opacity: 1,
              y: 0,
              duration: 1,
              ease: 'power2.out',
            });
          } else {
            gsap.to(entry.target, {
              opacity: 0,
              y: 50,
              duration: 1,
              ease: 'power2.out',
            });
          }
        });
      },
      { threshold: 0.1 }
    );

    textRefs.current.forEach((el) => observer.observe(el));
    return () => textRefs.current.forEach((el) => observer.unobserve(el));
  }, []);

  return (
    <section className="scroll-reveal-section">
      <p
        className="reveal-text"
        ref={(el) => (textRefs.current[0] = el)}
      >
        Discover innovative solutions tailored for you.
      </p>
      <p
        className="reveal-text"
        ref={(el) => (textRefs.current[1] = el)}
      >
        Build with creativity and precision.
      </p>
      <p
        className="reveal-text"
        ref={(el) => (textRefs.current[2] = el)}
      >
        Join a community of forward-thinkers.
      </p>
    </section>
  );
};

export default ScrollRevealSection;