import React, { useEffect } from 'react';
import { gsap } from 'gsap';
import '../styles/HeroSection.css';

const HeroSection = () => {
  useEffect(() => {
    // Main Title Animation
    gsap.fromTo(
      '.hero-title',
      { opacity: 0, y: 50 },
      { opacity: 1, y: 0, duration: 1, ease: 'power2.out', stagger: 0.2 }
    );

    // Background Circles Animation
    gsap.fromTo(
      '.circle',
      { scale: 0, opacity: 0 },
      { scale: 1, opacity: 0.3, duration: 1.5, stagger: 0.3, ease: 'power2.out' }
    );

    // Parallax Effect on Mouse Move
    const handleMouseMove = (e) => {
      const circles = document.querySelectorAll('.circle');
      circles.forEach((circle, index) => {
        const speed = 0.1 * (index + 1); // Inner circles move faster
        const x = (window.innerWidth / 2 - e.clientX) * speed;
        const y = (window.innerHeight / 2 - e.clientY) * speed;
        gsap.to(circle, { x, y, duration: 0.5 });
      });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  return (
    <section className="hero-section">
      <div className="circle circle-1"></div>
      <div className="circle circle-2"></div>
      <div className="circle circle-3"></div>
      <h1 className="hero-title">Smarter Solutions</h1>
      <h1 className="hero-title">Powered by AI</h1>
      <p>Streamline operations, reduce costs, and scale effortlessly with <br/>our AI-driven tools.</p>
      <button className="cta-button">Get Started</button>
    </section>
  );
};

export default HeroSection;